import {MapViewState, EventState, getDefaultEventState} from "../MapViewState";

export abstract class MapState {
    protected eventCreationZoomMinLevel = 11.7;
    protected triggerStateChange;
  
    constructor(triggerStateChange) {
      this.triggerStateChange = triggerStateChange;
    }
  
    abstract onMapClick(e, viewState: MapViewState, setViewState): MapState;
    abstract render(viewState: MapViewState, setViewState: any): JSX.Element;
  }
  